<script>
import {get} from "@/api/http";
import SysLogo from "@/components/logo.vue";

export default {
  name:"SysIndex",
  components: {SysLogo},
  data(){
    return{
      infos:{},
      disks:{
        usage:'-',
        total:'-',
        disk_usage:0
      },
      msg:true,
    }
  },
  methods:{
    init(){
      get('/info').then(res=>{
        res === null
            ? this.$message.error("获取数据失败")
            : this.$message.success("获取数据成功");
        this.msg = res !== null;
        this.infos = res
        this.disks = res['disk']
      }).catch(()=>{
        this.$message.error("请求失败")
      })
    },
    progressColor(value) {
      if (value < 20) {
        return '#5cb87a';
      } else if (value > 20 && value < 40) {
        return '#1989fa';
      } else if (value >= 40 && value <= 80) {
        return '#e6a23c';
      }else {
        return '#f56c6c'
      }
    }
  },
  created() {
    setInterval(this.init, 3000);
  },
}
</script>

<template>
  <div class="common-layout">
    <el-container>
      <el-header style="background-color: #e5e7ec;display: flex; justify-content: flex-start;">
        <SysLogo></SysLogo>
      </el-header>
      <el-main style="background-color: #f7f7f7;" v-loading="msg">
        <div style="">
          <el-card class="box-card">
            <template #header>
              <div class="card-header">
                <span>{{infos['hostname']}}</span>
                <el-dropdown trigger="click">
                  <el-button type="primary" >
                    操作<el-icon class="el-icon--right"><arrow-down /></el-icon>
                  </el-button>
                  <template #dropdown>
                    <el-dropdown-menu>
                      <el-dropdown-item icon="Link" @click="this.$message.error('连不了一点')">远程连接</el-dropdown-item>
                    </el-dropdown-menu>
                  </template>
                </el-dropdown>
              </div>
            </template>
            <div>
              <p><span class="key">主机名:</span><span class="value">{{infos['hostname']}}</span></p>
              <p><span class="key">系统平台:</span><span class="value">{{infos['system_type']}}</span></p>
              <p><span class="key">系统版本:</span><span class="value">{{infos['os']}}</span></p>
              <p><span class="key">CPU:</span><span class="value">{{infos['cpu']}}</span></p>
              <el-progress
                  :color="progressColor(infos['cpu_usage'])"
                  :text-inside="true"
                  :stroke-width="24"
                  :percentage="infos['cpu_usage']"
              />
              <p><span class="key">内存:</span><span class="value">{{infos['used_memory']}} / {{infos['memory']}}</span></p>
              <el-progress
                  :color="progressColor(infos['memory_usage'])"
                :text-inside="true"
                :stroke-width="24"
                :percentage="infos['memory_usage']"
              />
              <p><span class="key">系统盘:</span><span class="value">{{disks['usage']}} / {{disks['total']}}</span></p>
              <el-progress
                  :color="progressColor(disks['disk_usage'])"
                  :text-inside="true"
                  :stroke-width="24"
                  :percentage="disks['disk_usage']"
              />
              <p><span class="key">系统运行时长:</span><span class="value">{{infos['uptime']}}</span></p>
              <p><span class="key">网络信息:</span></p>
              <div style="padding: 10px">
                <div style="margin: 5px" v-for="(value, key) in infos['networks']" :key="key">
                  <span class="key">{{ key }}:</span><span class="value">{{ value }}</span>
                </div>
                <div style="margin: 5px" v-for="(value, key) in infos['macs']" :key="key">
                  <span class="key">{{ value.net }}:</span><span class="value">{{ value.mac }}</span>
                </div>
                <el-divider />
              </div>

              <p><span class="key">连接数量:</span></p>
              <div style="padding: 10px">
                <div style="margin: 5px">
                  <span class="key">TCP:</span><span class="value">{{ infos['tcp'] }}</span>
                  <el-divider direction="vertical" />
                  <span class="key">UDP:</span><span class="value">{{ infos['udp'] }}</span>
                </div>
                <el-divider />
              </div>

              <p><span class="key">实时速率:</span></p>
              <div style="padding: 10px">
                <div v-for="ipInfo in infos['network_speeds']" :key="ipInfo.ip[0]" style="margin: 5px">
                  <span class="value">
                    net: {{ ipInfo['net'] }}<el-divider direction="vertical" />ip: {{ ipInfo.ip[0] }}<br>
                    <el-button-group>
                      <el-button class="custom-button" icon="Upload" text>{{ ipInfo['up'] }}</el-button>
                      <el-button class="custom-button" icon="Download" text>{{ ipInfo['down'] }}</el-button>
                    </el-button-group>
                  </span>
                </div>
                <el-divider></el-divider>
              </div>

              <p><span class="key">总上/下行:</span></p>
              <div style="padding: 10px">
                <div v-for="ipInfo in infos['network_traffic']" :key="ipInfo.ip[0]" style="margin: 5px">
                  <span class="value">
                    net: {{ ipInfo['net'] }}<el-divider direction="vertical" />ip: {{ ipInfo.ip[0] }}<br>
                    <el-button-group>
                      <el-button class="custom-button" icon="Upload" text>{{ ipInfo['up'] }}</el-button>
                      <el-button class="custom-button" icon="Download" text>{{ ipInfo['down'] }}</el-button>
                    </el-button-group>
                  </span>
                </div>
                <el-divider></el-divider>
              </div>
            </div>
          </el-card>
        </div>
      </el-main>
    </el-container>
  </div>
</template>

